body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  object-fit: cover;
}

* {
  scroll-behavior: smooth;
}

.is-scrollable {
  height: 100%;
  overflow: scroll;
}

.prism-code {
  text-align: left;
  margin: 1em 0;
  padding: 0.5em;
}

.token-line {
  line-height: 1.5em;
  height: auto;
}

.token-line .tag {
  background-color: transparent;
  padding-left: 0;
  padding-right: 0.2em;
  height: auto;
}

.code.lineNo {
  display: inline-block;
  width: 2em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  opacity: 0.3;
}
